<template>
  <div class="wrapper wrapper-bg">
    <div class="w-menu">
      <div class="btn btn-l" v-if="navIndex!==0" @click="handleMenuChange('left')"></div>
      <div class="btn btn-r" v-if="navIndex!==2" @click="handleMenuChange('right')"></div>
      <div class="navbar">
        <ul class="ulbar" :style="{width:routeWrapperParam.length*200+'px',marginLeft:-navIndex*280+'px'}">
          <router-link
            v-for="(item, index) in this.routeWrapperParam"
            :key="index"
            :class="routerIndex === index ? 'item active': 'item'"
            :to="item.link"
            tag="li"
            @click.native="routerChange(index, item)"
          >{{item.eachName}}
          </router-link>
        </ul>
      </div>
      
    </div>
    <ul class="btnUl">
      <li class="btnLi">
        <router-link
          :to="{path: '/zdyztfx/scfxbg', query: {eachName: eachName}}"
          tag="span"
          active-class="active"
        >生成分析报告
        </router-link>
      </li>
    </ul>
    <transition>
      <router-view :params='eachName'> 
      </router-view>
    </transition>
  </div>
</template>

<script>
import { get_http } from "@/api/index";

export default {
  name: "lf",
  components: {},
  data() {
    return {
      eachName: '',
      navIndex: 0,
      routerIndex: 0,
      routeWrapperParam: [
        { link: "/zdyztfx/szxjjaj", eachName: "涉众型经济案件" },
        { link: "/zdyztfx/zfbzyfdc", eachName: "住房保障与房地产" },
        { link: "/zdyztfx/ldsb", eachName: "劳动社保" },
        { link: "/zdyztfx/nctdzy", eachName: "农村土地征用" },
        { link: "/zdyztfx/shce", eachName: "扫黑除恶" },
      ],
    };
  },
  watch: {
    '$route.path': {
      handler:function(path,npath){
        if (path === '/zdyztfx/szxjjaj') {
          this.routerIndex = 0
        } else if(path === '/zdyztfx/zfbzyfdc') {
          this.routerIndex = 1
        } else if(path === '/zdyztfx/ldsb') {
          this.routerIndex = 2
        } else if(path === '/zdyztfx/nctdzy') {
          this.routerIndex = 3
        } else if(path === '/zdyztfx/shce') {
          this.routerIndex = 4
        }
        this.eachName = this.routeWrapperParam[this.routerIndex].eachName
      },
      immediate: true
    }
  },
  methods: {
    handleMenuChange(dir) {
      dir === 'left' ? this.navIndex-- : this.navIndex++;
    },
    routerChange(index, item) {
      // 记录路由点击下表
      this.routerIndex = index;
      // 记录点击的名称传入生成分析报告中
      this.eachName = item.eachName
    },
  },
  beforeRouteUpdate (to, from, next) {
    let path = to.path;
    console.log(to)
    if (path === '/zdyztfx/szxjjaj') {
      this.routerIndex = 0
    } else if(path === '/zdyztfx/zfbzyfdc') {
      this.routerIndex = 1
    } else if(path === '/zdyztfx/ldsb') {
      this.routerIndex = 2
    } else if(path === '/zdyztfx/nctdzy') {
      this.routerIndex = 3
    } else if(path === '/zdyztfx/shce') {
      this.routerIndex = 4
    }
    this.eachName = this.routeWrapperParam[this.routerIndex].eachName
    next()
  },
};
</script>

